'use client'
import Link from 'next/link'
import { useState } from 'react'

export default function Navbar() {
  const [open, setOpen] = useState(false)

  return (
    <nav className="bg-white/70 backdrop-blur sticky top-0 z-40 shadow-sm">
      <div className="container mx-auto flex items-center justify-between py-4">
        <Link href="/" className="font-bold text-xl">
          Venturemond
        </Link>

        <div className="hidden md:flex gap-6 items-center">
          <Link href="/services" className="hover:text-accent transition">Services</Link>
          <Link href="/projects" className="hover:text-accent transition">Projects</Link>
          <Link href="/about" className="hover:text-accent transition">About</Link>
          <Link
            href="/#contact"
            className="px-4 py-2 bg-gradient-to-r from-indigo-500 to-sky-500 text-white rounded-lg shadow"
          >
            Contact
          </Link>
        </div>

        <button className="md:hidden" onClick={() => setOpen(!open)}>
          â˜°
        </button>
      </div>

      {open && (
        <div className="md:hidden px-4 pb-4 space-y-2">
          <Link href="/services" className="block py-1">Services</Link>
          <Link href="/projects" className="block py-1">Projects</Link>
          <Link href="/about" className="block py-1">About</Link>
        </div>
      )}
    </nav>
  )
}
